<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    return {
      columns: [
          {
            title: 'ID',
            key: 'uid',
            align: 'center'
          },
          {
            title: '操作人',
            key: 'username',
            align: 'center'
          },
          {
            title: '操作类别',
            key: 'dotype',
            align: 'center'
          },
          {
            title: '时间',
            key: 'addtime',
            align: 'center'
          },
          {
            title: '备注',
            key: 'logtext',
            align: 'center',
            width: 500,
          }
      ],
      data: [],
      totalPages: 0,
      tableData: [],
      isLoading: true,
      params: {}
    }
  },
  mounted: function() {
    const token = getToken(),
            uid = this.$route.query.uid,
            params = {"token": token, "uid": uid, "page": 0, "per_page": 12};

    this.params = params;
    this.getData(params);
  },
  methods: {
    getData: function(params) {
      api.adminLog(params).then(res => {
      console.log(res)
      if(res.code === 0) {
        this.isLoading = false
        this.totalPages = res.data.totlerows
        this.data = res.data.loglist
        this.data.forEach(itm => {
          itm.addtime = this.$moment(itm.addtime*1000).format("YYYY-M-DD k:mm")
        })
      } else {
        this.isLoading = false
        this.$Notice.error({
          title: res.msg
        })
      }
    })
    },
    changePage: function(page) {
      this.params.page = page;
      this.getData(this.params);
    },
    changePageNum: function(num) {
      console.log(num)
      this.params.per_page = num;
      this.getData(this.params);
    },
  }
}
</script>
<template>
  <div>
    <h3><Icon type="ios-book" size=24 />操作日志</h3>
    <Table :columns="columns" :data="data" :loading=isLoading></Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer :page-size=12 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
</template>
